import React from "react";
import { useState, useEffect, useRef } from "react";
import { useStore } from "react-hookstore";

function Meter() {
  const [speed, setSpeed] = useStore("speedStore");
  const [_, setMeterspeeds] = useState(0);
  let meterspeed = useRef(0);

  const reset = () => {
    if (meterspeed.current !== 0) {
      setSpeed(
        1000 - Math.abs(meterspeed.current) * 10 + Math.floor(Math.random() * 4)
      );
      meterspeed.current = 0;
      setMeterspeeds(0);
    }
  };
  const slowReset = () => {
    if (meterspeed.current === 0) {
    } else if (meterspeed.current > 0) {
      meterspeed.current--;
      setMeterspeeds(speed => speed - 1);
    } else if (meterspeed.current < 0) {
      meterspeed.current++;
      setMeterspeeds(speed => speed + 1);
    }
    if (meterspeed.current <= 1 && meterspeed.current >= -1) {
      meterspeed.current = 0;
      setMeterspeeds(0);
    }
  };
  const setFromEvent = e => {
    if (Math.floor(meterspeed.current + e.deltaY / 100) < -100) {
      meterspeed.current = -100;
      setMeterspeeds(meterspeed.current);
    } else if (Math.floor(meterspeed.current + e.deltaY / 100) > 100) {
      meterspeed.current = 100;
      setMeterspeeds(meterspeed.current);
    } else {
      meterspeed.current = Math.floor(meterspeed.current + e.deltaY / 100);
      setMeterspeeds(meterspeed.current);
    }
  };
  useEffect(() => {
    const interval = setInterval(slowReset, 100);
    window.addEventListener("wheel", setFromEvent);
    return () => {
      clearInterval(interval);
      window.removeEventListener("wheel", setFromEvent);
    };
  }, [speed]);

  return (
    <div id="meterBox">
      <button onClick={reset}>Spin!</button>
      <div
        style={{
          border: "1px solid black",
          width: "200px",
          height: "20px",
          background: "white",
          display: "flex",
          justifyContent: "center"
        }}
      >
        <span
          style={{
            background: "red",
            paddingLeft:
              meterspeed.current > 0 ? meterspeed.current + "px" : "0px",
            marginLeft:
              meterspeed.current > 0 ? meterspeed.current + "px" : "0px",
            paddingRight:
              meterspeed.current < 0 ? -meterspeed.current + "px" : "0px",
            marginRight:
              meterspeed.current < 0 ? -meterspeed.current + "px" : "0px",
            height: "20px",
            display: "block"
          }}
        ></span>
      </div>
    </div>
  );
}

export { Meter };
