import React, { useEffect } from "react";
import { useStore } from "react-hookstore";

function Wheel(props) {
  const [data, setData] = useStore("dataStore");
  const [speed, setSpeed] = useStore("speedStore");
  let newData = data;
  let thisSpeed = speed;
  let spin = () => {
    if (thisSpeed > 0) {
      document
        .getElementById("wheel")
        .prepend(document.getElementById("wheel").lastElementChild);
      [...document.getElementsByClassName("whoption")].forEach(e => {
        e.style.transitionDuration = thisSpeed + "ms";
      });
      thisSpeed = Math.ceil(thisSpeed + thisSpeed / 10);
      newData.unshift(newData.pop());
      if (thisSpeed < 800) {
        setTimeout(spin, thisSpeed);
      } else {
        setTimeout(() => {
          setData(newData);
          setSpeed(0);
        }, thisSpeed);
      }
    }
  };
  useEffect(() => {
    spin();
  });
  return (
    <wheel id="wheel" style={{ transitionDuration: `${thisSpeed}ms` }}>
      {newData.map(e => {
        return (
          <whoption
            className="whoption"
            key={e.id}
            style={{
              backgroundColor: e.color,
              transitionDuration: `${thisSpeed}ms`
            }}
          >
            <p>{e.text}</p>
          </whoption>
        );
      })}
    </wheel>
  );
}

export { Wheel };
